.intro h1 {
}

.intro h1::after {
    /* position: absolute; */
    transition: .3s;
    content: '';
    bottom: 0;
    left: 0;
    margin: auto;
    height: 4px;
    width: 0;
    display: block;
    background: var(--primary);
    animation: slidein 2s;
    animation-delay: .5s;
    animation-fill-mode: forwards;
    transform: translateY(-5px);
}

@keyframes slidein {
    0% {
        width: 0;
    }

    50% {
        width: 100%;
    }

    100% {
        width: 100%;
    }
}

section h2 {
    text-align: center;
    margin-bottom: .8rem;
}

.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: auto;
    padding-bottom: 50vh;
    height: 90vh;
    animation: shrink .8s;
    animation-delay: 2.5s;
    animation-fill-mode: forwards;
}

.social {
    margin-top: 1.5rem;
}

.social>ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: center;
    padding-inline-start: 0;
}

.ic-social {
    color: var(--primary);
    padding: 0 7px;
    font-size: 2rem;
}

.ic-social:hover {
    color: var(--link);
    transition: .3s ease-out;
}

@keyframes shrink {
    100% {
        height: 30vh;
        padding-bottom: 4rem;
    }
}

.intro p {
    margin: auto;
}

.about-content {
    margin: auto;
}

@media only screen and(min-width: 1080px) {
    .about-img {
        max-height: 20vw;
    }
}