footer .navbar-nav {
    justify-content: space-around;
    padding-bottom: 0 !important;
    padding-top: 1rem;
}

.line {
    height: 2px;
    background-color: var(--primary);
}

.right {
    justify-content: space-around;
    display: flex;
    flex-direction: row;
    margin-top: 1rem;
}

footer h4,
footer h4 {
    text-align: center;
}

.contact>ul {
    padding-inline-start: 25px;
    display: flex;
    flex-direction: row;
    list-style: none;
    padding-inline-start: 0;
}

.contact>ul>li {
    padding: 0 7px;
    font-size: 1.2rem;
}

footer .nav-item {
    padding-top: 0;
    font-size: 1.3rem;
}

@media only screen and (max-width: 1090px) {
    footer {
        flex-direction: column;
    }

    footer>div>nav>.navbar-nav {
        flex-direction: row;
        padding-bottom: .8rem;
    }

    .right {
        padding-left: 0;
        padding-right: 0;
    }
}