.container {
    width: 50%;
    margin: auto;
}

header {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
}

main {
    padding: 2rem 0 5rem 0;
    
}

main>section {
    margin: auto;
}

footer {
    margin-top: 4rem;
    padding-bottom: 6rem;
    display: flex;
    flex-direction: column;
}

.mb-1 {
    margin-bottom: 1rem;
}

.mb-2 {
    margin-bottom: 1.5rem;
}

@media only screen and (max-width: 1500px) {
    .container {
        width: 70%;
    }

    img {
        max-height: 35vw !important;
    }
}

@media only screen and (max-width: 720px) {
    .container {
        width: 90%;
    }
}