.navbar {
    margin: auto;
}

.navbar a:hover {
    color: var(--primary);
}

.navbar-nav {
    display: flex;
    list-style: none;
}

.nav-item {
    padding: 1rem;
    margin: 0 1rem;
    position: relative;
    overflow: hidden;
    transition: .3s;
}

.nav-item:hover {
    cursor: pointer;
}

.nav-item>a:hover {
    color: var(--primary);
}

.nav-item::after {
    position: absolute;
    transition: .3s;
    content: '';
    width: 0;
    left: 50%;
    bottom: 0;
    height: 2px;
    background: var(--primary);
}

.nav-item:hover::after {
    width: 100%;
    left: 0;
}

.nav-link {
    text-decoration: none;
    font-size: 1.2rem;
    color: var(--primary);
    font-weight: 700;
}